  methods: {
    //使用标志
    async ncftpgetss() {
      console.log(this.regsId);

      const res = await this.$http
        .post(
          `CarInspector/word/mark/${this.regsId}`,
          { headers: "application/x-download" },
          { responseType: "blob" }
        )
        .then((response) => {
          console.log(response);
          if (response.status === 200) {
            this.getUserlist();
            // 提示
            this.$message({
              type: "success",
              message: "下载成功",
            });
            const blob = new Blob([response.data], {
              type: "application/msword",
            });
            const fileName = "使用标志打印模板.pdf";

            const elink = document.createElement("a");

            elink.download = fileName;

            elink.style.display = "none";

            elink.href = URL.createObjectURL(blob);

            document.body.appendChild(elink);

            elink.click();

            URL.revokeObjectURL(elink.href); // 释放URL 对象
            document.body.removeChild(elink);
            this.dengdialogFormVisible = false;
          } else if (res.data.code == 201) {
            this.$message({
              type: "danger",
              message: "请填写所需要的内容",
            });
          }
        })
        .catch((err) => {
          this.$message({
            type: "danger",
            message: "请填写所需要的内容",
          });
          //         _toastr.error(err)
        });
    },
    savencftpgetss() {
      this.loadingbutss = true;
      this.loadingbuttextss = "加载中...";
      this.$http.get(`carword/mark?id=${this.shiyongId}`).then((res) => {
        console.log(res);
        if (res.data.code == 1) {
          this.$message({
            type: "warning",
            message: res.data.msg,
          });
          this.loadingbutss = false;
          this.loadingbuttextss = "保存使用标志";
        } else if (res.data.code == 200) {
          this.$message({
            type: "success",
            message: res.data.msg,
          });
          this.codes = res.data.code;
          this.url = res.data.data;
          // this.urls = res.data.data[1];
          this.loadingbutss = false;
          this.loadingbuttextss = "保存使用标志";
        }
      });
    },
    async sybiaozhi(row) {
      console.log(row);
