    //   添加用户发送请求
    async AddUser() {
      // 2.关闭对话框
      this.$refs.form.validate(async (valid) => {
        if (typeof this.form.equType === "string") {
          this.form.equType = this.form.equType;
          console.log(this.form.equType);
        } else {
          if (this.form.equType.length == 1) {
            this.form.equType = this.form.equType[0];
            console.log(this.form.equType);
          } else {
            this.form.equType = this.form.equType[1];
            console.log(this.form.equType);
          }
        }

        if (valid) {
          if (this.tyPe == 1) {
            this.form.sheng = sessionStorage.getItem("provinceCode"); //省code
            this.form.zoneId = sessionStorage.getItem("cityCode"); //市code
            this.form.zoneName = sessionStorage.getItem("country");
            const res = await this.$http.post(`/carDoucument/save`, this.form);
            if (res.data.code === 200) {
              // 回到第一页展示
              //this.pagenum = 1;
              // 重新请求数据
              this.getUserlist();
              // 提示
              this.$message({
                type: "success",
                message: res.data.msg,
              });

              this.dialogFormVisibleAdd = false;
              this.administrative();
              // this.form = {};
              // this.last = {};
            } else {
              this.$message({
                type: "error",
                message: res.data.msg,
              });
            }
          } else if (this.tyPe == 2) {
            this.form.sheng = sessionStorage.getItem("provinceCode"); //省code
            this.form.zoneId = sessionStorage.getItem("cityCode"); //市code
            const res = await this.$http.post(`/carDoucument/save`, this.form);
            if (res.data.code === 200) {
              // 回到第一页展示
              //this.pagenum = 1;
              // 重新请求数据
              this.getUserlist();
              // 提示
              this.$message({
                type: "success",
                message: res.data.msg,
              });

              this.dialogFormVisibleAdd = false;
              this.administrative();
              // this.form = {};
              // this.last = {};
            } else {
              this.$message({
                type: "error",
                message: res.data.msg,
              });
            }
          } else if (this.tyPe == 3) {
            this.form.sheng = sessionStorage.getItem("provinceCode"); //省code
            const res = await this.$http.post(`/carDoucument/save`, this.form);
            if (res.data.code === 200) {
              this.getUserlist();
              // 提示
              this.$message({
                type: "success",
                message: res.data.msg,
              });

              this.dialogFormVisibleAdd = false;
              this.administrative();
              this.$message({
                type: "error",
                message: res.data.msg,
              });
            }
          } else {
            const res = await this.$http.post(`/carDoucument/save`, this.form);
            console.log(res);
            if (res.data.code === 200) {
              // 回到第一页展示
              //this.pagenum = 1;
              // 重新请求数据
              this.getUserlist();
              // 提示
              this.$message({
                type: "success",
                message: res.data.msg,
              });
              this.dialogFormVisibleAdd = false;
              this.administrative();
            } else {
              this.$message({
                type: "error",
                message: res.data.msg,
              });
            }
          }
        } else {
          this.$message({
            type: "error",
            message: "请填写内容再试",
          });
          this.dialogFormVisibleAdd = true;
        }
      });
    },

