            makeData: this.last.makeData, //制造日期
            pressure: this.last.pressure, //气瓶公称压力
            volume: this.last.volume, //容积
            checkData: this.last.checkData, //检查日期
            nextCheckData: this.last.nextCheckData, //下次检查日期
            selfId: this.last.selfId, //单位内部编号
            medium: this.last.medium, //介质
          };
          this.unlist.push(obj);
          this.form.lista.push(obj);
          console.log(1212);
          console.log(this.form.lista);
          this.dialogFormVisibleAdds = false;
        } else {
